import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusViewHttpService } from '../../../../http/custom-view/custom-view.http.service';
// import { SelectPartyRelationshipBean } from '../bean/selectPartyRelationship.bean';
import { AddPartyRelationshipBean } from '../../../../custom-view/association-relation/personal-relation/bean/addPartyRelationship.bean';
// import { Panduan, ReductionNumberType } from '../../../../constant/custView.codeValue';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Message } from 'primeng/primeng';
import { Router } from '@angular/router';
import *  as _ from 'lodash';
import { checkNumber } from 'app/@uisftech/common/service/validator';
@Component({
    selector: 'app-add-lagal',
    templateUrl: './add-lagal.component.html',
    styleUrls: ['./add-lagal.component.scss']
})
export class AddLagalComponent implements OnInit, OnChanges {
    @Output() outValue = new EventEmitter();
    @Input('in-value') inValue: any;
    @Input('key-label') keyLabel: any;
    @Input('kind-do') kindDo: string = '';
    // 
    addPartyRelationshipBean: AddPartyRelationshipBean = new AddPartyRelationshipBean();
    panduan: any[] = [];
    reductionNumberType: any[] = [];
    // relationshipType: any[] = RelationshipType;
    // parentTypeId: any[] = ParentTypeId;
    distributeCustQryForm: FormGroup;//
    checkNumber = checkNumber;//
    custNo: string;
    items: any[];
    parentId: any = [{label:'',value:'0101'}];
    parentTypeId: string;//
    relationshipType: string;//
    relationship: any = [{label:'',value:'010104'}];
    principalLinkman: any;
    principalLinkmanOrg: any;
    whether: boolean = true;
    whetherCredit: boolean = true;
    //
    msgs: Message[] = [];
    show: boolean = true;
    showEmpty: boolean = true;
    code:any;
    constructor(
        public httpService: CusViewHttpService,
        public fb: FormBuilder,
        public commfunc: Commfunc,
        public router: Router
    ) {
        this.codeVal();
        this.panduan=this.code['Panduan'];
        this.reductionNumberType=this.code['ReductionNumberType'];
        //--------------------------------------------------
        // this.distributeCustQryForm = fb.group({
        //   custNo: ['', Validators.required],
        // })

    }

    ngOnInit() {

        this.distributeCustQryForm = this.fb.group({
            'custNo': new FormControl('', Validators.required),//
            'custName': new FormControl('', Validators.required),//
            'relationshipCustNo': new FormControl('', Validators.required),//
            'relationshipCustName': new FormControl('', Validators.required),//
            'parentTypeId': new FormControl('', Validators.required),//
            'relationshipType': new FormControl('', Validators.required),//
            'whetherReductionNumber': new FormControl(''),//
            // 'reductionNumberType': new FormControl(''),//
            'whetherUnifiedCredit': new FormControl(''),//
            // 'whetherCreditCustomers': new FormControl(''),//
            // 'creditNumber': new FormControl(''),//
            // 'listSystemRecord': new FormControl(''),//
            // 'whetherValid': new FormControl(''),//
            'comments': new FormControl(''),//
            // 'principalLinkman': new FormControl(''),
            // 'principalLinkmanOrg': new FormControl('')
        });
    }

    ngOnChanges() {
        if (this.inValue) {
            // 
            this.addPartyRelationshipBean = new AddPartyRelationshipBean();
            this.addPartyRelationshipBean.custNo = this.inValue.custNo;
            this.addPartyRelationshipBean.weakIdenFlag = this.inValue.weakIdenFlag;
            this.addPartyRelationshipBean.custName = this.commfunc.getSessionDataCH('custName');
        }
    }

    //
    submitBtn() {
        this.addPartyRelationshipBean.parentTypeId = "0101";
        this.addPartyRelationshipBean.relationshipType = "010104";
        if (!this.addPartyRelationshipBean.relationshipCustNo) {
            this.showEmpty = false;
        }
        //
        for (const i in this.distributeCustQryForm.controls) {
            this.distributeCustQryForm.controls[i].markAsDirty();
        }
        // this.addPartyRelationshipBean.weakIdenFlag = '3';//
        // this.custNo = this.addPartyRelationshipBean.custNo;
        let ob = this.httpService.addPartyRelationship(this.addPartyRelationshipBean);
        ob.subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addPartyRelationshipBean = new AddPartyRelationshipBean();
                // this.router.navigate(['pages/tzb/workflow/custom-workflow/association-relation',data.dataMap]);
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '', detail: '' });
                this.outValue.emit('');
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: '' });
        })
    }
    //
    reset() {
        this.outValue.emit(false);
    }

    // 
    selectCustBriefInfo(event) {
        if (!this.addPartyRelationshipBean.relationshipCustNo) {
            this.showEmpty = false;
            return;
        }
        let param = {
            custNo: this.addPartyRelationshipBean.relationshipCustNo
        }
        this.httpService.selectCustBriefInfo(param).subscribe(
            data => {
                if (data.returnCode.code == 'success') {
                    this.addPartyRelationshipBean.relationshipCustName = data.custName;
                    this.principalLinkman = data.principalLinkmanName;
                    // this.principalLinkman = data.principalLinkmanName+"("+data.principalLinkman+")";
                    this.principalLinkmanOrg = data.principalLinkmanOrgName;
                    // this.principalLinkmanOrg = data.principalLinkmanOrgName + "(" + data.principalLinkmanOrg + ")";
                } else {
                    this.show = false
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: '' });
            })
    }
    keyupNum(event) {
        this.showEmpty = true;
        this.show = true;
        if (checkNumber(null, event.target.value)) {
            event.target.value = '';
        }

    }
      //
      codeVal() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code= ret //code
    }
}